<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-image round class="avatar" :src="profileObj.photo"  @click="imageCilckFn"/>
          <input type="file" ref="iptFile" v-show="false"
          accept="image/*" @change="onFileChange">
        </template>
      </van-cell>
      <van-cell title="名称" is-link  :value="profileObj.name" @click="nameClickFn"/>
      <van-cell title="生日" is-link  :value="profileObj.birthday" @click="birthdayClickFn" />
          </van-cell-group>
          <van-dialog
      v-model="show"
      title="标题"
      show-cancel-button
      :before-close="beforeCloseFn"
    >
      <input type="text" v-fos v-model="inputUserName" />
    </van-dialog>
    <van-popup v-model="dateTimePickerShow" position="bottom" style="height: 50%" round>
      <!-- 日期选择控件 -->
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择出生日期"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="dateCancelFn"
        @confirm="confirmFn"
      />
</van-popup>

  </div>
</template>

<script>
import { userProfileAPI, updateUserPhotoAPI, updateUserProfileAPI } from '@/api'
import { Notify } from 'vant'
import { mapMutations } from 'vuex'
import { formatDate } from '@/utils/date.js'
export default {
  name: 'UserEdit',
  data () {
    return {
      profileObj: {},
      show: false,
      inputUserName: '',
      minDate: new Date(1920, 0, 1), // 最小的可选的日期
      maxDate: new Date(), // 最大的可选日期
      currentDate: new Date(),
      dateTimePickerShow: false
    }
  },
  async created () {
    const res = await userProfileAPI()
    console.log(res)
    this.profileObj = res.data.data
  },
  methods: {
    ...mapMutations(['SET_USERPHOTO']),
    async onFileChange (e) {
      if (e.target.files.length === 0) return
      console.log(e.target.files[0])
      const theFd = new FormData()
      theFd.append('photo', e.target.files[0])
      const res = await updateUserPhotoAPI(theFd)
      console.log(res)
      this.profileObj.photo = res.data.data.photo
      this.SET_USERPHOTO(res.data.data.photo) // 更新成功后, 同步到vuex中
    },
    imageCilckFn () {
      this.$refs.iptFile.click()
    },
    nameClickFn () {
      this.show = true
      this.inputUserName = this.profileObj.name
    },
    async beforeCloseFn (action, done) {
      if (action === 'confirm') {
        const reg = /^[a-zA-Z0-9\u4e00-\u9fa5]{1,7}$/
        if (reg.test(this.inputUserName) === true) {
          await updateUserProfileAPI({
            name: this.inputUserName
          })
          this.profileObj.name = this.inputUserName
          done()
        } else {
          Notify({ type: 'warning', message: '用户名只能是1-7位中英数字组合' })
          done(false)
        }
      } else {
        done()
      }
    },
    birthdayClickFn () {
      this.dateTimePickerShow = true
      this.currentDate = new Date(this.profileObj.birthday)
    },
    dateCancelFn () {
      this.dateTimePickerShow = false
    },
    async confirmFn () {
      await updateUserProfileAPI({
        birthday: formatDate(this.currentDate)
      })
      this.dateTimePickerShow = false
      this.profileObj.birthday = formatDate(this.currentDate)
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
::v-deep .van-dialog__content{
    text-align: center;
    input{
        padding: 0;
        outline:none;
        border: none;
        text-align: center;
    }
}
</style>
